* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	background-color: white;
}

li, a {
	font-family: 'Karla', sans-serif;
	font-size: 16px;
	color: #a0a0a0;
}

nav {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	/*padding: 30px 10%;*/
	padding: 30px 7%;
	padding-left: 8%;
	
	/*border-style: solid;*/
	/*border-color:red;*/
}

.nav-link {
	list-style: none;
}

.menu li {
	display: inline-block;
	padding: 0 20px
}


.nav-link {
	text-decoration: none;
}

.nav-link:hover {
	color: #ef9135;
}

.logo {
	margin-right: auto;
	font-size: 20px;
}

.underline-link {
	text-decoration: none;
	padding-bottom: 5px;
	border-bottom: 4px solid #ef9135;
}

* {
	margin: 0;
	padding: 0;
}

.container {
	display: flex;
	width: 85%;
	padding: 2%;
	margin: 0 auto;
	/*border: 1px solid black;*/
}

.caption {
	position: absolute;
	background-color: transparent;
	padding-left: 5%;
	padding-top: 4%;
	width:30%;
	font-family: 'Karla', sans-serif;
	font-size: 1.6vw;
	/*font-size: 1.5em;*/
	color: white;
	font-weight: 50;
	text-align: center;
}

.image {
	width: 100%;	
	background: url(/Nasita-Haque.github.io/static/media/bike-bg.f5708dc9.jpg) no-repeat;
	background-size: contain;
	height: 750px;
	/*border: 1px solid black;*/
}

.project-container {
	display: flex;
	justify-content: center;
	flex-direction: column;
	width: 85%;
	padding: 2%;
	margin: 0 auto;
	font-family: 'Karla', sans-serif;
	font-size: 1.6vw;
	color: #a0a0a0;
	text-align: center;
}

h3 {
	padding-bottom: 5%;
}

img {
	height: 100%;
    width: 50%;
}

.title {
  text-align: center;
  display: block;
  padding-bottom: 5%;
  font-size: 2vw;
}

.about-container {
	display: flex;
	width: 85%;
	padding: 2%;
	padding-top: 5%;
	margin: 0 auto;
	justify-content: center;
	/*justify-content: flex-end;*/
	font-family: 'Montserrat', sans-serif;
}

.description {
    margin-left: 1%;
	/*padding-top: 5%;*/
	/*width: 28%;*/
	width: 50%;
	text-align: left;
	color: #a0a0a0;
	font-family: 'Karla', sans-serif;
	/*margin-right: auto;
*/	/*border: 1px solid black;*/
}

.description h2 {
	color: #a0a0a0;
	padding-bottom: 2%;
	font-size: 1.6vw;
}

.description h3 {
	color: #a0a0a0;
	padding-bottom: 6%;
	font-size: 1.2vw;
}

.description p {

	font-weight: 300;
	line-height: 150%
}

.intro {
	font-weight: 400;
	padding-bottom: 5%;
	font-size: 1.1vw;
}

.body {
	padding-bottom: 5%;
	font-size: 1.1vw;
}

.email {
	font-size: 1.1vw;
}

.email a {
	color: #ef9135;
	font-size: 1.1vw;
}

.profile-container{
    margin-right: 1%;
	width: 30%;
	/*padding-top: 2%;*/
	/*margin-right: auto;*/

}

.profile {
	width: 100%;	
	background: url(/Nasita-Haque.github.io/static/media/profile.bec89e04.JPG) no-repeat;
	background-size: contain;
	height: 400px;
	/*border: 1px solid black;*/
}


.footer {
	display: flex;
	justify-content: center;
	margin-top: 2%;
	margin-bottom: 2%;
	/*border-top: 1px solid currentcolor;*/
	position: relative;
	bottom: 0;
	text-decoration: none;
	background-color: rgb(239,145,53);
	height: 2%;
}

.footer-menu li {
	text-decoration: none;
	display: inline-block;
	padding: 0 20px;
	background-color: transparent;
	font-size: 1vw;
}

.footer-menu li, a {
	text-decoration: none;
	background-color: transparent;
}

.footer-menu li, a:hover {
	color: #ef9135;
}

.img-container img{
	display: flex;
	width: 85%;
	padding: 2%;
	padding-top: 5%;
	margin: 0 auto;
	justify-content: center;
}
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.page {
	position: relative;
	min-height: 100vh;
}
